<template>
  <div class="index-box">
    <div class="header">
      <div class="box1">
        <div class="logo"></div>
      </div>
    </div>
    <div class="content">
      <h2>{{data.title}}</h2>
      <div v-for="item in data.content">
        <p v-if="item.type === 'p'">{{item.text}}</p>
        <pre v-if="item.type === 'j'"><span class="sign">JavaScript</span><code data-language="JavaScript">{{item.text}}</code></pre>
        <pre v-if="item.type === 'c'"><span class="sign">CSS</span><code data-language="css">{{item.text}}</code></pre>
        <pre v-if="item.type === 'h'"><span class="sign">HTML</span><code data-language="html">{{item.text}}</code></pre>
      </div>
    </div>
  </div>
</template>

<script>
  import data from './data/test';
  export default {
    name: 'app',
    data () {
      return {
        data: data
      }
    },
    methods: {}
  };

</script>

<style lang="less" scoped>
  @import '../lib/swiper/swiper.css';
  @import '../lib/reset.css';
  @import '../lib/monokai.css';

  .index-box {
    background: #fff;
    width: 100%;
    height: 100%;
    min-width: 275px;
    .content {
      width: 100%;
      max-width: 1000px;
      margin: 0 auto;
      padding: 10px;
      box-sizing: border-box;
      font-size: 15px;
      color: #333;
      line-height: 25px;
      p {
        text-indent: 30px;
        margin: 5px 0;
      }
      pre {
        width: 100%;
        padding: 10px;
        background: #272822;
        box-sizing: border-box;
        color: #fff;
        border-radius: 5px;
        line-height: 16px;
        margin: 10px 0;
        font-size: 0;
        position: relative;
        .sign {
          position: absolute;
          right: 10px;
          top: 10px;
          color: #888;
          font-size: 12px;
        }
        code {
          font-size: 12px;
        }
      }
    }
    .header {
      width: 100%;
      height: 51px;
      color: #333;
      background: #fff;
      .box1 {
        background: #fff;
        width: 100%;
        margin: 0 auto;
        height: 50px;
        border-bottom: 1px solid #eee;
        position: fixed;
        z-index: 1000;
        .logo {
          width: 100%;
          max-width: 500px;
          height: 100%;
          margin: 0 auto;
          background: url("https://raw.githubusercontent.com/beautifulBoys/beautifulBoys.github.io/master/source/home/blog.jpg") no-repeat center center;
          background-size: 35%;
        }
      }
    }
  }


</style>



