var data = {
  "title": "JavaScript事件委托",
  "content": [
    {
      "type": "p",
      "text": `使用事件委托技术能让你避免对特定的每个节点添加事件监听器；相反，事件监听器是被添加到它们的父元素上。事件监听器会分析从子元素冒泡上来的事件，找到是哪个子元素的事件。基本概念非常简单，但仍有很多人不理解事件委托的工作原理。这里我将要解释事件委托是如何工作的，并提供几个纯JavaScript的基本事件委托的例子。`
    },
    {
      "type": "h",
      "text": `<div class="ul-box">
  <ul>
    <li>第一条数据</li>
    <li>第二条数据</li>
    <li>第三条数据</li>
    <li>第四条数据</li>
    <li>第五条数据</li>
  </ul>
</div>`
    },
    {
      "type": "j",
      "text": `var ul = document.getElementsByTagName('ul')[0];
ul.onclick = function (e) {
  var e = e || window.event;
  var target = e.target || e.srcElement;
  if (target.nodeName.toLowerCase() === 'li') {
    console.log(target.innerHTML);
  }
}`
    },
    {
      "type": "c",
      "text": `html, body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}
ul {
  width: 100%;
  padding: 0;
  margin: 0;
}
li {
  line-height: 40px;
  border-bottom: 1px solid #aaa;
  text-align: center;
  color: #333;
}`
    },
    {
      "type": "p",
      "text": `当每个li被点击是，上方没有绑定事件，事件向上冒泡到ul，触发事件。事件中通过target拿到被点击的元素。`
    }
  ]
};
module.exports = data;




